import { Injectable } from '@angular/core';
import { MenuService } from 'src/app/service/core/menu/menu.service';
import { ResourceService } from 'src/app/service/core/resource/resource.service';
import { AuthService } from '../auth/auth.service';
import { SettingsService } from '../setting/setting.service';
import { AppInfo } from '../setting/models/app-info';
import { UserInfo } from '../auth/models/auth-info';
@Injectable({ providedIn: 'root' })
export class StartupService {
  private currentUser: UserInfo;

  constructor(
    private menuService: MenuService,
    private resourceService: ResourceService,
    private settingsService: SettingsService,
    private authService: AuthService
  ) {}

  async load() {
    const app: AppInfo = {
      name: '后台管理',
      description: '统一后台管理'
    };

    const user: UserInfo = {
      id: '1',
      userName: '123',
      passWord: '123',
      email: '11111'
    };
    this.authService.currentUser.subscribe(x => (this.currentUser = x));
    this.settingsService.setApp(app);
    await this.menuService.load(user.id);
    await this.resourceService.load(user.id);
  }
}
